#grid1 {
  display: grid;
  grid-template-rows: repeat(4, 1fr);
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 10px;
}

#grid1 > div {
  font-size: 4vw;
  padding: 10px;
}

body {
  background-image: linear-gradient(
    to bottom,
    rgb(233, 196, 152),
    rgb(177, 194, 214)
  );
}
